@charset "utf-8";

@import "basic/basic";
@import "blocks/header";
@import "blocks/footer";


.first-screen {
  background: $accent-color;
  &__slider {

  }
}

.slider {
  &__item {
    padding-top: 140px;
    padding-bottom: 100px;
    background: url(../img/content/img1.png) no-repeat right bottom / 60%;
  }
  &__item .container {
    display: flex;
  }
  &__content {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__intro-text {
    font-size: 1.9rem;
    font-weight: 700;
    color: #ffffff;
    margin-bottom: 25px;
  }
  &__title {
    font-family: 'A City Nova';
    font-size: 8rem;
    font-weight: 700;
    color: #201600;
    margin-bottom: 40px;
    position: relative;
    z-index: 5;
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      bottom: 15%;
      left: -20px;
      background-color: #fff;
      width: 97%;
      height: 20%;
    }
  }
  &__text {
    font-size: 1.3rem;
    color: #201600;
    margin-bottom: 55px;
    max-width: 430px;
  }
  &__btn {
    text-transform: uppercase;
    border: 2px solid #ffffff;
    font-size: 1.9rem;
    color: #ffffff;
    border-radius: 40px;
    & > a {
      padding: 15px 70px 12px;
      display: inline-block;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 104px;
    height: 104px;
    border-radius: 50%;
    opacity: 0.6;
    background: no-repeat center / contain;
    &:hover {
      opacity: 1;
    }
  }
  .swiper-button-prev {
    background-image: url(../img/icon/ar_left.png);
  }
  .swiper-button-next {
    background-image: url(../img/icon/ar_right.png);
  }
  .swiper-pagination {
    margin-left: 10vw;
    // margin-left: 50px;
    text-align: left;
  }
  .swiper-pagination-bullet {
    width: 20px;
    height: 20px;
    background-color: transparent;
    border: 1px solid #ffffff;
    opacity: 1;
    &-active {
    background-color: #ffffff;
    }
  }
}


.about {
  padding-top: 100px;
  padding-bottom: 100px;
  &__row {
    display: flex;
    justify-content: space-between;
  }
  &__img {
    flex-basis: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__content {
    flex-basis: 50%;
    color: #201600;
    padding: 0 30px;
  }
  &__title {
    font-size: 2.8rem;
    font-weight: 700;
    margin-bottom: 30px;
  }
  &__text {
    line-height: 21px;
    & > p {
      margin-bottom: 30px;
    }
  }
  &__info {
    font-size: 1.4rem;
    line-height: 18px;
    color: #666666;
    margin-bottom: 65px;
    & a {
      font-weight: 700;
      color: #5e85bf;
    }
  }
  &__btn {
    & a {
      padding: 15px 22px;
    }
  }
}

.features {
  background-color: $main-grey;
  padding: 80px 0 90px;
  &__title {
    font-size: 2.8rem;
    font-weight: 700;
    color: $accent-color;
    text-align: center;
    margin-bottom: 65px;
  }
  &__row {
    padding: 0 100px;
  }
}
.features-list {
  display: flex;
  justify-content: space-between;
  &__item {
    flex-basis: 19%;
    display: flex;
    flex-direction: column;
    align-items: center;
    & img:first-child {
      display: block;
    }
    & img:last-child {
      display: none;
    }
    &:hover img:first-child {
      display: none;
    }
    &:hover img:last-child {
      display: block;
    }
    
    &:hover .features-list__img {
      background-color: $accent-color;
    }
    &:hover .features-list__text {
      color: $accent-color;
    }
  }
  &__img {
    transition: .1s;
    width: 170px;
    height: 170px;
    width: 170px;
    border: 3px solid $accent-color;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
  }
  &__text {
    transition: .1s;
    line-height: 20px;
    color: #ffffff;
    text-align: center;
    font-weight: 700;
  }
}

.conditions {
  padding: 75px 0;
  background: url(../img/content/img2.png) no-repeat 90% bottom / auto;
