    }
  }
}
.conditions-list {
  &__item {
    line-height: 22px;
    margin-bottom: 26px;
  }
}

.calculate {
  padding: 70px 110px;
  &__title {
    font-size: 2.8rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 60px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__calculator {
