.pledge {
  background-color: $light-grey;
  padding: 70px 0 60px;
  &__title {
    font-size: 2.8rem;
    font-weight: 700;
    color: #000000;
    margin-bottom: 55px;
    text-align: center;
  }
  &__row {}
}

.pledge-list {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  &__item {
    background-color: #fff;
    flex-basis: calc(25% - 15px);
    padding: 40px;
    padding-top: 15px;
    border-radius: 10px;
    position: relative;
  }
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 115px;
    margin-bottom: 25px;
  }
  &__title {
    line-height: 19px;
    font-weight: 700;
    color: #000000;
    text-align: center;
    min-height: 40px;
  }
  &__text {
    position: relative;
    text-align: center;
    padding-top: 30px;
    font-size: 1.4rem;
    line-height: 17px;
    color: #646464;
    &::before {
      content: '';
      position: absolute;
      top: 15px;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 80%;
      height: 1px;
      border: 1px solid rgba(0, 0, 0, 0.25);
    }
    display: none;
    &--shown {
      animation: showText .4s ease;
      display: block;
    }
  }
  &__more {
    display: block;
    width: 60px;
    height: 60px;
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    bottom: -20px;
    left: calc(50% - 30px);
    cursor: pointer;
    &::before,
    &::after {
      content: '';
      display: block;
      width: 10px;
      height: 2px;
      position: absolute;
      bottom: 10px;
      background-color: #cccccc;
      border-radius: 1px;
      transition: .4s;
    }
    &::before {
      left: 20px;
      transform-origin: right center;
      transform: rotate(45deg);
    }
    &::after {
      left: 30px;
      transform-origin: left center;
      transform: rotate(-45deg);
    }
    &--shown {
      &::before,
      &::after {
        bottom: 17px;
      }
      &::before {
        left: 20px;
        transform-origin: right center;
        transform: rotate(-45deg);
      }
      &::after {
        left: 30px;
        transform-origin: left center;
        transform: rotate(45deg);
      }
    }
  }
}

@keyframes showText {
  from {opacity: 0;}
  to {opacity: 1;}
}

