.geo {
  overflow: hidden;
  position: relative;
  &::before,
  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 0;
    position: absolute;
    z-index: 10;
    box-shadow: 0 0 80px 10px #000000;
  }
  &::before {
    left: 0;
    top: 0;
  }
  &::after {
    left: 0;
    bottom: 0;
  }
  &__buttons {
    position: absolute;
    z-index: 20;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between;
  }
  &__btn {
    // box-shadow: 0px 0px 40px 0px rgba(1,1,1,0.12);
    cursor: pointer;
    width: 65px;
    height: 65px;
    background-color: #ffffff;
    border: 1px solid #dbdbdb;
    border-radius: 10px;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color .2s;
    &:not(:last-child) {
      margin-right: 12px;
    }
    &:hover {
      background-color: $accent-color;
    }
    & img:first-of-type {
      display: block;
    }
    & img:last-of-type {
      display: none;
    }
    &:hover img:first-of-type {
      display: none;
    }
    &:hover img:last-of-type {
      display: block;
    }
    
    // &::before {
    //   content: '';
    //   width: 70%;
    //   background-color: grey;
    //   height: 100%;
    //   position: absolute;
    //   z-index: -1;
    //   top: 0;
    //   left: 0;
    //   display: block;
    //   // box-shadow: 0px 0px 40px 0px rgba(1,1,1,0.12);
    // }
  }
  &__info {
    position: absolute;
    z-index: 10;
    background-color: #fff;
    bottom: 100%;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 40px 0px rgba(1,1,1,0.12);
    // border: 1px solid #dbdbdb;
    border-radius: 5px;
    transform: translateY(-10px);
    padding: 20px 15px;
    width: 235px;
    text-align: center;
    font-family: 'MyriadPro', sans-serif;
    color: #6d6e71;
    opacity: 0;
    visibility: hidden;
    transition: .2s;
    &::before {
      content: '';
      --size: 50px;
      display: block;
      position: absolute;
      z-index: -10;
      bottom: 0;
      left: 50%;
      background-color: #fff;
      width: var(--size);
      height: var(--size);
      transform: translateX(-50%) scaleX(1.5) rotate(45deg);
    }
    &--active {
      opacity: 1 !important; 
      visibility: visible;
    }
  }
  &__btn:hover &__info {
    opacity: .5;
    visibility: visible;
  }
}
