$accent-color: #f5da46;
$main-grey: #888888;
$light-grey: #e8e8e8;


:root {
  font-size: 10px;
}
body {
  font-size: 1.6rem;
  font-weight: 400;
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin: auto;
}
.btn {
  &--convex {
    background-image: linear-gradient(to top, #F6DB4B, #FAE77E);
    border-radius: 30px;
    box-shadow: inset 1px -2px 3px 1px #fff9da;
    font-weight: 700;
    color: #201600;
    display: inline-block;
    white-space: nowrap;
    &:hover {
      color: #fff;
      box-shadow: inset 0 0 5px 2px $accent-color;
      background: $accent-color;
    }
    & a {
      display: inline-block;
    }
  }
}

.yellow-mark-list {
  & li {
    position: relative;
    margin-left: 30px;
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -30px;
      width: 13px;
      height: 13px;
      background-color: $accent-color;
      border-radius: 50%;
    }
  }
}

.scroll-up {
  --size: 50px;
  width: var(--size);
  height: var(--size);
  position: fixed;
  z-index: 200;
  right: 30px;
  bottom: 30px;
  background-color: rgba(167, 167, 167, 0.45);
  border-radius: 50%;
  & > a {
    display: block;
    width: 100%;
    height: 100%;
    // opacity: 0;
    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      background-color: #fff;
      height: 4px;
      width: 20px;
      left: 15px;
      top: 55%;
      border-radius: 2px;
    }
    &::before {
      transform: rotate(-45deg);
      transform-origin: left 6px;
    }
    &::after {
      transform: rotate(45deg);
      transform-origin: right 6px;
    }
  }
  &--shown {
    animation: showScrollBtn .5s forwards;
  }
  &--hidden {
    animation: hideScrollBtn .5s forwards;
  }
}

@keyframes showScrollBtn {
  0% {
    transform: translateX(150px);

  }
  100% {
    transform: translateX(0);
  }
}

@keyframes hideScrollBtn {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(150px);
  }
}