.footer {
  background-color: #3b3b3b;
  &__inner {
    padding: 100px 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    // flex-basis: 49%;
  }
  &__contact {
    // flex-basis: 49%;
  }
  &__social {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;

  }
  &__social-title {
    font-size: 3.1rem;
    font-weight: 700;
    color: $accent-color;
  }
  &__social-btns {
    display: flex;
    align-items: center;
  }
  &__social-btn {
    margin-left: 45px;
    transition: .2s;
    &:hover {
      opacity: .6;
    }
  }
  &__form {

  }
}

.footer-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #ffffff;
  border-radius: 10px;
  padding: 10px;
  &__tel {
    position: relative;
    z-index: 10;
    & > input {
      padding: 15px;
      background-color: transparent;
      font-size: 1.8rem;
      border: none;
      outline: none;
      color: #ffffff;
    }
  }
  &__btn {
    & > input {
      background-color: $accent-color;
      border-radius: 10px;
      border: none;
      padding: 15px;
      font-size: 1.4rem;
    }
  }
}