.header {
  &__upper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
  }
  &__burger {
    display: none;
    --size: 60px;
    width: var(--size);
    height: var(--size);
    border: 2px solid #888888;
    border-radius: 5px;
    position: relative;
    & > span {
      transition: .3s;
      position: absolute;
      left: calc(50% - 20px);
      top: calc(50% - 11px);
      background-color: #888888;
      height: 2px;
      width: 40px;
      &::before,
      &::after {
        content: '';
        width: 100%;
        height: 100%;
        background-color: #888888;
        position: absolute;
        left: 0;
        transform-origin: center;
      }
      &::before {
        top: 10px;
      }
      &::after {
        top: 20px;
      }
    }
    &--active {
      & > span {
        top: calc(50% - 1px);
        transform: rotate(45deg);
        &::before {
          opacity: 0;
        }
        &::after {
          top: 0;
          transform: rotate(-90deg);
        }
      }
    }
  }
  &__menu {
    background-color: #888888;
  }
  &__logo {}
  &__contacts {
    display: flex;
    align-items: center;
  }
  &__phone {
    font-size: 1.9rem;
    color: #5d73d5;
    display: flex;
    align-items: center;
    position: relative;
    &:before {
      content: url(../img/icon/phone.png);
      margin-right: 10px;
    }
    &:hover:after {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 81%;
      height: 2px;
      background-color: #5d73d5;
    }
  }
  &__call-btn {
    margin-left: 40px;
  }
  &__call-btn a {
    padding: 14px 30px;
  }
}
.nav-menu {
  display: flex;
  &__item {
    font-size: 1.7rem;
    color: #ffffff;
    transition: .2s;
    &:hover {
      background-color: $accent-color;
      color: #201600;
      background-image: linear-gradient(to top, #fff2aa, $accent-color);
    }
  }
  &__item a {
    display: inline-block;
    padding: 15px 10px;
  }
}

.burger-menu {
  display: none;
  background-color: #888888;
  position: fixed;
  z-index: 200;
  height: 100%;
  width: 100%;
  animation: showMenu .3s;
  &--active {
    display: block;
  }
  & .nav-menu {
    flex-direction: column;
    align-items: center;
    height: 100%;
    font-weight: 700;
    &__item {
      padding-top: 8px;
    }
  }
}
@keyframes showMenu {
  from {opacity: 0;}
  to {opacity: 1;}
}